import React from "react";
const Select = (props) => {
  return (
    <select
      onChange={props.onChange}
      name="product"
      id="product"
      defaultValue={1}
    >
      <option value="SELECT">-- Select --</option>
      <option value="LECHE DESN">LECHE DESN</option>
      <option value="SMP">SMP</option>
      <option value="SMP-ST">SMP-ST</option>
      <option value="MP80">MP80</option>
      <option value="MP85">MP85</option>
      <option value="MCI90">MPI90</option>
      <option value="MC85">MC85</option>
      <option value="MCI90">MCI90</option>
      <option value="WPI90">WPI90</option>
    </select>
  );
};
export default Select;
